* {
	padding: 0;
	margin: 0;
}
a {
	color: #000;
	text-decoration: none;
}
body {
	background-color: #ffdf8c;
}
.wrapper {
	height: 100%;
	background-color: #fff;
	border-radius: 10px;
	max-width: 1400px;
	width: calc(100vw - 100px);
	margin: 50px auto;
}
.header {
	width: 90%;
	margin: 0 auto;
	padding: 30px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.logo-wrapper a {
	display: flex;
	align-items: center;
	text-decoration: none;
}
.logo__text {
	margin-left: 10px;
}
.search__wrapper {
	border: 1px solid #000;
	border-radius: 3px;
	display: flex;
	align-items: center;
	flex-flow: 1;
}
.search__wrapper input {
	border: none;
	padding-left: 5px;
	font-size: 19px;
	line-height: 30px;
}
.search__wrapper input:focus {
	outline: none;
}
.card-wrapper {
	min-width: 317px;
	display: flex;
	justify-content: flex-end;
}
.card {
	display: flex;
	align-items: center;
	background-color: #ffdf8c;
	border-radius: 10%;
	padding: 10px;
	font-weight: 600;
}
.card__total-price {
	padding: 0 5px;
}
.card__count-prod {
	display: flex;
	align-items: center;
	border-left: 1px groove rgba(0, 0, 0, 0.253);
	padding: 0 5px;
}
.categories ul {
	list-style: none;
	display: flex;
	width: 90%;
	margin: 0 auto;
	padding: 30px;
}
.category {
	border: 1px solid #ffdf8c;
	border-radius: 40px;
	padding: 8px 10px;
	margin-right: 10px;
	transition: 0.3s all ease;
	font-size: 18px;
	line-height: 18px;
	cursor: pointer;
}
.category:hover {
	background-color: #ffdf8c;
}
.category.active {
	background-color: #ffdf8c;
}
.sort-wrapper {
}
.sort__label-link {
	border-bottom: 1px solid #ffdf8c;
}
.sort {
	display: flex;
	flex-direction: column;
	border-radius: 10px;
	background-color: #fff;
	border: 1px solid #ffdf8c;
	max-width: min-content;
	padding: 5px 10px;
}
.sort-input {
	padding: 3px 7px;
	font-size: 15px;
	border: none;
	transition: 0.3s all ease;
}
.sort-input:hover {
	background-color: #ffdf8c;
}
